.input {
  background: #EDEDED;
  border-radius: 36px;
  border: 0;
  padding: 21.5px 20px;
  font-size: 14px;
  width: 223px;
  &__box {
    position: relative;
    width: 263px;
  }
  &__button {
    width: 40px;
    height: 40px;
    background: #FFC85E;
    border-radius: 1000px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    top: 10px;
    right: 10px;
    border: 0;
  }
}

@media (max-width: 1199px) {
  .input {
    width: 200px;
    &__button {
      top: 10px;
      right: 40px;
    }
  }
}

@media (max-width: 991px) {
  .input {
    width: 180px;
    &__button {
      top: 10px;
      right: 60px;
    }
  }
}

@media (max-width: 767px) {
  .input {
    width: 160px;
    &__button {
      top: 10px;
      right: 80px;
    }
  }
}
